        { name: "华语组合", type: 1003 },
        { name: "欧美男歌手", type: 2001 },
        { name: "欧美女歌手", type: 2002 },
        { name: "欧美组合", type: 2003 },
        { name: "日本男歌手", type: 6001 },
        { name: "日本女歌手", type: 6002 },
        { name: "日本组合", type: 6003 },
        { name: "韩国男歌手", type: 7001 },
        { name: "韩国女歌手", type: 7002 },
        { name: "韩国组合", type: 7003 },
        { name: "其他男歌手", type: 4001 },
