        this.catlist();
        return;
      }
      if (this.$store.state.activeName === "anchor") {
        // 调用电台轮播方法
        this.anchor_banner_fn();
        // 调用电台分类方法
        this.anchor_type_fn();
        // 创作翻唱
        this.select_nchor_type_title_fn();
        // 音乐故事
        this.music_story_fn();
        // 声音恋人
        this.voice_lover_fn();
        return;
