        // 调用飙升榜的接口
        this.top_list_top();
        // 全球榜  获取所有榜单 接口:  /toplist
        this.all_toplist_data_fn();
        return;
      }

      // 歌手区
      if (this.$store.state.activeName === "singer" && this.aa === 0) {
        this.aa++;
        this.singer_data_fn_scroll_fn();
        return;
      }
      if (this.$store.state.activeName === "latest") {
        this.select_song_zj_fn();
      }
