    // 格式化时间
    init_time(times) {
      var t = "";
      if (times > -1) {
        var min = Math.floor(times / 60) % 60;
        var sec = times % 60;
        if (min < 10) {
          t += "0";
        }
        t += min + ":";
        if (sec < 10) {
          t += "0";
        }
        t += sec.toFixed(2);
      }
      t = t.substring(0, t.length - 3);
      return t;
    },

